@charset "UTF-8";
/* Colores */
/* End Colores */
/* html-body */
html {
  box-sizing: border-box;
  background-color: #454360;
  font-size: 62.5%; /* 1rem = 10px */
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Sofia Sans", sans-serif;
  font-size: 1.6rem;
  line-height: 2;
}

/* End html-body */
/** Globales **/
.contenedor {
  max-width: 120rem;
  width: 100%;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: var(--blanco);
}

h1, h2, h3 {
  font-weight: 900;
  font-family: var(--fuenteSecundaria);
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.5rem;
}

h3 {
  font-size: 2.8rem;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

/** End Globales **/
