.navbar {
  font-size: 1.8rem;
}
@media (min-width: 768px) {
  .navbar {
    display: flex;
    justify-content: space-between;
    padding-bottom: 0;
    height: 7rem;
    align-items: center;
  }
}

.navbar-toggle {
  position: absolute;
  top: 0.5rem;
  right: 2rem;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.8);
  font-size: 2.4rem;
}
@media (min-width: 768px) {
  .navbar-toggle {
    display: none;
  }
}

.logo {
  display: inline-block;
  margin-top: 1rem;
  margin-left: 4rem;
}
@media (min-width: 768px) {
  .logo {
    margin-top: 0;
    margin-left: 8rem;
  }
}

#chkToggle {
  display: none;
}

#chkToggle:checked + ul.main-nav {
  display: block;
}
@media (min-width: 768px) {
  #chkToggle:checked + ul.main-nav {
    display: flex;
  }
}

.main-nav {
  list-style-type: none;
  display: none;
}
@media (min-width: 768px) {
  .main-nav {
    display: flex;
    margin-right: 30px;
    flex-direction: row;
    justify-content: flex-end;
  }
}

.main-nav li {
  text-align: center;
  margin: 15px auto;
}
@media (min-width: 768px) {
  .main-nav li {
    margin: 0;
  }
}

.navegacion__link {
  font-weight: 900;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .navegacion__link {
    padding: 1rem 2rem;
  }
}

.navegacion__link--activo {
  color: #FFD15C;
}

.navegacion__link--hover:hover {
  color: #FFD15C;
}

/* Whatsapp */
.float {
  position: fixed;
  width: 60px;
  height: 60px;
  bottom: 40px;
  right: 40px;
  background-color: #25d366;
  color: #FFF;
  border-radius: 5rem;
  text-align: center;
  font-size: 30px;
  box-shadow: 2px 2px 3px #999;
  z-index: 100;
}

.float:hover {
  background-color: #2BB741;
}

.my-float {
  margin-top: 16px;
}

/* End Whatsapp */
/* división */
.division {
  border-top: 1px solid var(--blanco);
}

/* End división */
/* section home */
.home {
  border-top: 1px solid #FFFFFF;
  background: linear-gradient(-45deg, #454360, #FF4C60, #FFD15C, #353353);
  background-size: 400% 400%;
  animation: gradient 5s ease infinite;
  /* End section home */
}
@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.home .home__contenedor {
  padding: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 2.5rem;
}
.home .text-name {
  color: #FFFFFF;
  text-align: center;
  line-height: 2;
}
.home .text-description {
  text-align: center;
  color: #FFFFFF;
  font-size: 2rem;
}
.home .icons {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .home .icons {
    flex-direction: row;
  }
}
.home .img-icons {
  width: 7rem;
}
@media (min-width: 768px) {
  .home .img-icons {
    width: 6rem;
    margin-top: 2rem;
    margin-left: 2rem;
  }
}
.home .neon {
  -webkit-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
}
.home .neon:hover {
  box-shadow: 0 0 10px 0 #FFFFFF inset, 0 0 20px 2px #FFFFFF;
  border: 3px solid #FFFFFF;
  border-radius: 0.5rem;
  color: #FFFFFF;
}
.home .content-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .home .content-btn {
    padding: 2.5rem 0;
    flex-direction: row;
    gap: 1rem;
  }
}
.home .btn {
  background-color: transparent;
  box-shadow: 0 0 10px 0 #FFFFFF inset, 0 0 20px 2px #FFFFFF;
  border-radius: 3rem;
  border: 0.3rem solid #FFFFFF;
  padding: 1rem 1rem;
  line-height: 1;
  font-size: 2rem;
  -webkit-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
  color: #FFFFFF;
  margin: 0.5rem;
}
@media (min-width: 768px) {
  .home .btn {
    font-size: 1.6rem;
    -webkit-transition: all 0.15s ease-in-out;
    transition: all 0.15s ease-in-out;
  }
}
.home .btn:hover {
  box-shadow: 0 0 10px 0 #FFFFFF inset, 0 0 20px 2px #FFFFFF;
  border: 3px solid #FFFFFF;
  background-color: #FFFFFF;
  color: #454360;
}

/* section about */
.about {
  margin-top: 5rem;
}

.about__contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: linear-gradient(to bottom, transparent 50%, #FF4C60 50%, #FF4C60 100%);
  padding-top: 1.5rem;
}
@media (min-width: 768px) {
  .about__contenedor {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    gap: 2rem;
    background-image: linear-gradient(to right, transparent 25%, #FF4C60 25%, #FF4C60 100%);
    background-size: 100%;
    border-radius: 2rem;
  }
}

.me {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .me {
    margin-right: 10rem;
  }
}

.about__text {
  margin: 2rem 0.2rem;
  padding-bottom: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .about__text {
    margin-bottom: 2rem;
    font-size: 2rem;
    padding-bottom: unset;
  }
}

.about__contenedor p {
  color: #FFFFFF;
}

.title_about {
  color: #FFFFFF;
  margin: 0;
  font-size: 5rem;
}

.about__dark {
  background-color: #FF4C60;
}

.barra-progreso {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
}

.title_developer {
  color: #FFFFFF;
  margin: 1rem;
  padding: 1rem;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .title_developer {
    font-size: 2rem;
  }
}

.barra_container {
  width: 100%;
  border-radius: 3rem;
  margin-top: 1.5rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .barra_container {
    margin: 0 1.5rem 3rem 0;
  }
}

.barra_skills {
  color: white;
  border-radius: 3rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .barra_skills {
    display: none;
  }
}

/* End section about */
/* section experience */
.experience {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .experience {
    margin-top: 5rem;
  }
}

.experience-title {
  text-align: center;
  padding: 2rem 0;
  position: relative;
}
.experience-title h2 {
  font-size: 2.5rem;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 20px;
  padding-bottom: 0;
  color: #FFFFFF;
  position: relative;
  z-index: 2;
}
@media (min-width: 768px) {
  .experience-title h2 {
    font-size: 3.2rem;
  }
}

.experience__contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .experience__contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}

.resume .resume-title {
  font-size: 2rem;
  font-weight: 700;
  margin-top: 2rem;
  margin-bottom: 20px;
  color: #FFFFFF;
}

.resume .resume-item {
  padding: 0 0 2rem 2rem;
  padding-bottom: 2rem;
  margin-top: -0.2rem;
  border-left: 0.2rem solid #566777;
  position: relative;
}

.resume .resume-item::before {
  content: "";
  position: absolute;
  width: 1.6rem;
  height: 1.6rem;
  border-radius: 5rem;
  left: -9px;
  top: 0;
  background: #FFFFFF;
  border: 0.2rem solid #82909c;
}

.experience__company {
  font-size: 2rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .experience__company {
    font-size: 2.5rem;
    color: #FFFFFF;
  }
}

.experience__time, .experience__post {
  font-size: 2rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .experience__time, .experience__post {
    font-size: 2rem;
    color: #FFFFFF;
  }
}

.fa-laptop-code, .fa-calendar, .fa-user-tie {
  font-size: 2rem;
  margin: 0 0.5rem;
}
@media (min-width: 768px) {
  .fa-laptop-code, .fa-calendar, .fa-user-tie {
    font-size: 2.5rem;
    margin: 0 1rem;
  }
}

@media (min-width: 768px) {
  .resume .resume-title {
    font-size: 2.2rem;
  }
  .experience__content {
    margin-left: 10rem;
  }
}
.content_btn {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .content_btn {
    padding: 2.5rem;
    flex-direction: row;
    justify-content: center;
  }
}

.btn_cv {
  background-color: transparent;
  box-shadow: 0 0 10px 0 #FFFFFF inset, 0 0 20px 2px #FFFFFF;
  border-radius: 3rem;
  border: 0.3rem solid #FFFFFF;
  padding: 1rem 1rem;
  line-height: 1;
  font-size: 2rem;
  -webkit-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
  color: #FFFFFF;
  margin: 0.5rem;
}
@media (min-width: 768px) {
  .btn_cv {
    font-size: 1.6rem;
    -webkit-transition: all 0.15s ease-in-out;
    transition: all 0.15s ease-in-out;
  }
}

.btn_cv:hover {
  box-shadow: 0 0 10px 0 #FFFFFF inset, 0 0 20px 2px #FFFFFF;
  border: 3px solid #FFFFFF;
  background-color: #FFFFFF;
  color: #454360;
}

/* Trabajos */
.productos {
  padding: 5rem 1.5rem;
}

.producto__descripcion {
  text-align: center;
  margin: 2rem 0.3rem 0rem 0.3rem;
  border-radius: 3rem;
}
@media (min-width: 768px) {
  .producto__descripcion {
    margin-bottom: 0;
  }
}

.producto__descripcion img {
  max-width: 100%;
  border-top-left-radius: 3rem;
  border-top-right-radius: 3rem;
  transition-property: transform background-size;
  transition-duration: 0.3s;
}

.producto__descripcion:hover img {
  transform: scale(1.1);
  background-size: 3rem;
}

.producto__descripcion a {
  display: block;
  text-align: center;
  padding: 2rem;
  font-size: 2rem;
  color: #222038;
  background-color: #FFD15C;
  border-bottom-left-radius: 3rem;
  border-bottom-right-radius: 3rem;
}

.producto__descripcion a:hover {
  background-color: #FF4C60;
  color: #FFFFFF;
  border-bottom-left-radius: 3rem;
  border-bottom-right-radius: 3rem;
}

@media (min-width: 768px) {
  .productos__listado {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
