.social-media .fab.fa-facebook-f:hover {
  background: #3b5998;
  color: #FFFFFF;
}

.social-media .fab.fa-linkedin-in:hover {
  background: #0083be;
  color: #FFFFFF;
}

.social-media .fab.fa-whatsapp:hover {
  background: #25d366;
  color: #FFFFFF;
}

.contacteme_contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  background-color: rgba(24, 0, 56, 0.5);
  border-radius: 2.5rem;
}
@media (min-width: 768px) {
  .contacteme_contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    background-color: #222038;
    border-radius: 2.5rem;
  }
}

.contFrm {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .contFrm {
