/* Footer */
.footer {
  margin-top: 2rem;
  border-top: 0.1rem solid #FFFFFF;
}

.footer_contenedor {
  display: block;
  padding: 1rem 0;
  text-align: center;
  text-transform: uppercase;
  font-size: 1.5rem;
  color: #FFFFFF;
}

/* End Footer */
