/* Newsletter */
.newsletter {
  padding: 5rem;
}

.newsletter_content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding: 1.5rem;
  border-color: rgb(229, 231, 235);
}

.newsletter_content_generalli {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  border-radius: 1rem;
}

@media (min-width: 768px) {
  .newsletter_content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-content: center;
  }
}
.works_btn_title {
  color: #FFD15C;
  font-size: 2rem;
  font-weight: bold;
  margin-left: 1rem;
  margin-bottom: 0.5rem;
}

@media (min-width: 768px) {
  .works_btn_title {
    font-size: 2rem;
  }
}
.newsletter_information p {
  font-size: 1.6rem;
  line-height: 2rem;
  text-align: left;
  color: #FFFFFF;
  margin-top: 1rem;
  margin-bottom: 1.5rem;
  margin-left: 1rem;
}

.ul-li {
  color: #FFFFFF;
  list-style: none;
  text-align: center;
}

.li {
  background-color: #FFD15C;
  border-radius: 0.5rem;
  margin: 1rem 3rem 1rem 3rem;
  padding: 1rem 1.5rem 1rem 1.5rem;
}

.a {
  color: #454360;
}

.a:hover {
  color: #FFFFFF;
}

.li:hover {
  background-color: #FF4C60;
}

@media (min-width: 768px) {
  .li {
    margin: 1rem 6rem 1rem 6rem;
    padding: 1rem;
  }
}
/* End Newsletter */